import React, { useEffect, useRef, useState } from 'react';
import { Link, navigate, useStaticQuery, graphql } from 'gatsby';
import { ReactComponent as LogoSmall } from '../../images/varendiHeader.svg';
import { ReactComponent as LogoExtraSmall } from '../../images/varendiXSmall.svg';
import { ReactComponent as LogoBig } from '../../images/varendiFooter.svg';
import { ReactComponent as MenuMobileBg } from '../../images/menu-mobile-bg.svg';
import { ReactComponent as BurgerIcon } from '../../images/burger-icon.svg';
import { ReactComponent as CrossIcon } from '../../images/cross-icon.svg';
import { phoneNumber, rawPhoneNumber, whatsAppNumber, rawWhatsAppNumber } from '../../utils/data';
import Tracking from '../../utils/tracking';
import { loadBing, loadGoogleAds } from '../../utils/pixel';
import useDocumentScrollThrottled from '../../utils/useDocumentScrollThrottled';
import './index.scss';

const languageMap = {
  technical: {
    en: 'Technical Translations',
    es: 'Traducciones Técnicas',
    pt: 'Traduções Técnicas'
  },
  certified: {
    en: 'Certified Translations',
    es: 'Traducciones Certificadas',
    pt: 'Traduções Juramentadas'
  }
};

const linksToGo = {
  technical: {
    en: '/technical-translation',
    es: '/traducciones-tecnicas',
    pt: '/'
  },
  certified: {
    en: '/sworn-translation',
    es: '/traducciones-publicas',
    pt: '/traducoes-juramentadas'
  }
};

const mapPagesToPath = {
  '/': {
    en: '/technical-translation',
    es: '/traducciones-tecnicas',
    pt: '/'
  },
  '/technical-translation': {
    en: '/technical-translation',
    es: '/traducciones-tecnicas',
    pt: '/'
  },
  '/technical-translation/': {
    en: '/technical-translation',
    es: '/traducciones-tecnicas',
    pt: '/'
  },
  '/traducciones-tecnicas': {
    en: '/technical-translation',
    es: '/traducciones-tecnicas',
    pt: '/'
  },
  '/traducciones-tecnicas/': {
    en: '/technical-translation',
    es: '/traducciones-tecnicas',
    pt: '/'
  },
  '/traducoes-juramentadas': {
    en: '/sworn-translation',
    es: '/traducciones-publicas',
    pt: '/traducoes-juramentadas'
  },
  '/traducoes-juramentadas/': {
    en: '/sworn-translation',
    es: '/traducciones-publicas',
    pt: '/traducoes-juramentadas'
  },
  '/sworn-translation': {
    en: '/sworn-translation',
    es: '/traducciones-publicas',
    pt: '/traducoes-juramentadas'
  },
  '/sworn-translation/': {
    en: '/sworn-translation',
    es: '/traducciones-publicas',
    pt: '/traducoes-juramentadas'
  },
  '/traducciones-publicas': {
    en: '/sworn-translation',
    es: '/traducciones-publicas',
    pt: '/traducoes-juramentadas'
  },
  '/traducciones-publicas/': {
    en: '/sworn-translation',
    es: '/traducciones-publicas',
    pt: '/traducoes-juramentadas'
  },
  '/trabalhe-conosco': {
    en: '/careers',
    es: '/trabaja-com-nosotros',
    pt: '/trabalhe-conosco'
  },
  '/trabalhe-conosco/': {
    en: '/careers',
    es: '/trabaja-com-nosotros',
    pt: '/trabalhe-conosco'
  },
  '/careers': {
    en: '/careers',
    es: '/trabaja-com-nosotros',
    pt: '/trabalhe-conosco'
  },
  '/careers/': {
    en: '/careers',
    es: '/trabaja-com-nosotros',
    pt: '/trabalhe-conosco'
  },
  '/trabaja-com-nosotros': {
    en: '/careers',
    es: '/trabaja-com-nosotros',
    pt: '/trabalhe-conosco'
  },
  '/trabaja-com-nosotros/': {
    en: '/careers',
    es: '/trabaja-com-nosotros',
    pt: '/trabalhe-conosco'
  },
};

const logoThreshold = 500;
const logoExtraSmallThreshold = 420;


const Header = ({ location, language = 'pt' }) => {
  const [showMenu, setShowMenu] = useState(false);
  const [shouldResizeHeader, setShouldResizeHeader] = useState(false);
  const [languageSelected, setLanguageSelected] = useState(language);
  const [logoBig, setLogoBig] = useState(typeof window  === 'object' && window.innerWidth > 500)
  const [logoExtraSmall, setLogoExtraSmall] = useState(typeof window  === 'object' && window.innerWidth < 420)
  const menuRef = useRef();
  const buttonRef = useRef();

  const { site } = useStaticQuery(
    graphql`
        query {
          site {
            siteMetadata {
              gaUA
            }
          }
        }
      `
  )

  useEffect(() => {
    Tracking.init(site.siteMetadata.gaUA)
    Tracking.pageView()
  }, [site.siteMetadata.gaUA])

  useEffect(() => {
    document.addEventListener('mousedown', handleClick);
    loadBing();
    loadGoogleAds();

    return () => {
      document.removeEventListener('mousedown', handleClick);
    };
  }, []);

  useDocumentScrollThrottled(callbackData => {
    const MINIMUM_SCROLL = 100;
    const TIMEOUT_DELAY = 400;
    const { currentScrollTop } = callbackData;

    setTimeout(() => {
      setShouldResizeHeader(currentScrollTop > MINIMUM_SCROLL);
    }, TIMEOUT_DELAY);
  });

  useEffect(() => {
    showMenu && (document.body.style.overflow = 'hidden');
    !showMenu && (document.body.style.overflow = 'unset');
 }, [showMenu]);

 useEffect(() => {
    function handleResize() {
      setLogoBig(window && window.innerWidth > logoThreshold);
      setLogoExtraSmall(window && window.innerWidth < logoExtraSmallThreshold);
    }

    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  const handleClick = ({ target }) => {
    const isInsideClick = menuRef.current.contains(target) || buttonRef.current.contains(target);
    if (isInsideClick) {
      return;
    }

    setShowMenu(false);
  };

  const handleLanguageChange = ({ target }) => {
    const languagueSel = target.value;
    setLanguageSelected(languagueSel);
    const pathname = location && location.pathname ? location.pathname : '/';
    navigate(mapPagesToPath[pathname][languagueSel]);
  };

  const scrollStyle = shouldResizeHeader ? '-resized' : '';

	return (
		<header class={`header${scrollStyle}`}>
			<nav class={`header--navigation-wrapper${showMenu ? '-open' : ''}`} ref={menuRef}>
        <MenuMobileBg class={`header--mobile-bg${showMenu ? '-open' : ''}`} />
				<div class={`header--mobile-menu-button${scrollStyle}`} ref={buttonRef}>
					<button class='header--mobile-menu-button' onClick={() => setShowMenu(!showMenu)}>
              <BurgerIcon class={`header--mobile-burger${showMenu ? '-open' : ''}`} />
              <CrossIcon class={`header--mobile-cross${showMenu ? '-open' : ''}`} />
              <path d="M0 3h20v2H0V3zm0 6h20v2H0V9zm0 6h20v2H0v-2z"/>
					</button>
				</div>
				<div class={`header--logo-container${scrollStyle}`}>
					<Link to='/'>
            {logoExtraSmall ? <LogoExtraSmall /> : !shouldResizeHeader && logoBig ? <LogoBig /> : <LogoSmall />}
					</Link>
				</div>
				<div class={`header--links-wrapper${showMenu ? '-open' : ''}`}>
					<Link to={linksToGo.technical[language]} class='header--links-link' activeClassName="active">
						<p>{languageMap.technical[language]}</p>
					</Link>
					<Link to={linksToGo.certified[language]} class='header--links-link' activeClassName="active">
            <p>{languageMap.certified[language]}</p>
					</Link>
          <a class='header--links-link-mobile' href={`tel:+55${rawPhoneNumber}`} onClick={Tracking.trackPhoneClick}>
            <p>{phoneNumber}</p>
          </a>
          <a class='header--links-link-mobile' href={`tel:+55${rawWhatsAppNumber}`} onClick={Tracking.trackPhoneClick}>
            <p>{whatsAppNumber}</p>
          </a>
				</div>
        <select
          class={`header--language-selector${showMenu ? '-open' : ''}`}
          value={languageSelected} 
          onChange={handleLanguageChange}
        >
          <option value='pt'>PT</option>
          <option value='en'>EN</option>
          <option value='es'>ES</option>
        </select>
			</nav>
		</header>
	);
};

export default Header;