
const getRawPhoneNumber = formattedNumber => formattedNumber.replace(/[.() -]/g, '')

export const phoneNumber = '(11) 5093-5050'
export const rawPhoneNumber = getRawPhoneNumber(phoneNumber)
export const whatsAppNumber = '(11) 9-7860-9053'
export const rawWhatsAppNumber = getRawPhoneNumber(whatsAppNumber)

export const email = 'webemail@varendi.com'

const getWhatsAppLink = (type) => `https://${type === 'web' ? 'web.whatsapp.com/send?phone=' : 'wa.me/'}55${rawWhatsAppNumber}${type === 'web' ? '&' : '?'}text=Oi, pessoal da Varendi! Gostaria de fazer um orçamento.`;

export const whatsAppLink = getWhatsAppLink();
export const whatsAppWebLink = getWhatsAppLink('web');
