import ReactGA from 'react-ga';

const track = (category, action, label) => {
  ReactGA.event({
    category,
    action,
    label,
  });
}

export default class {
  static init = (trackingID) => {
    ReactGA.initialize(trackingID);
  }

  static pageView = () => {  
    ReactGA.pageview(window.location.pathname + window.location.search);
  }

  static trackContactFormSubmitted() {
    track('formulario de contato', 'enviado');
  }

  static trackWorkWithUsFormSubmitted() {
    track('formulario de trabalhe conosco', 'enviado');
  }

  static trackWhatsAppClick() {
    track('contato', 'whatsapp');
  }

  static trackEmailClick() {
    track('contato', 'email');
  }

  static trackPhoneClick() {
    track('contato', 'telefone');
  }
}
