import React from 'react';
import RedPhoneIcon from '../../images/red-phone.png';
import MailIcon from '../../images/mail.png';
import WhatsappIcon from '../../images/whatsapp.png';
import Tracking from '../../utils/tracking';
import { email, rawPhoneNumber, whatsAppLink } from '../../utils/data';
import Mailto from '../../utils/obfuscate';
import './index.scss';

const languageMap = {
  phone: {
    en: 'Telephone',
    es: 'Teléfono',
    pt: 'Telefone'
  },
  email: {
    en: 'Email',
    es: 'Correo electrónico',
    pt: 'Email'
  },
  whatsApp: {
    en: 'WhatsApp',
    es: 'WhatsApp',
    pt: 'WhatsApp'
  },
  emailSubject: {
    en: 'I visited the Varendi.com website and would like more information',
    es: 'Visité el sitio web Varendi.com y me gustaría obtener más información.',
    pt: 'Visitei o site da Varendi.com e gostaria de mais informações'
  }
}

const FooterMobile = ({ language = 'pt' }) => {
  return (
    <footer class='footer-mobile--wrapper'>
      <a href={`tel:+55${rawPhoneNumber}`} onClick={Tracking.trackPhoneClick}>
        <img src={RedPhoneIcon} alt='phone' />
        <span>{languageMap.phone[language]}</span>
      </a>
      <Mailto
        email={email}
        obfuscate={true}
        obfuscatedHref='Enviar Email'
        headers={
          { subject: languageMap.emailSubject[language] }
      }>
      <img src={MailIcon} alt='email' />
      <span>{languageMap.email[language]}</span>
      </Mailto>
      <a href={whatsAppLink} target='_blank' onClick={Tracking.trackWhatsAppClick}>
        <img src={WhatsappIcon} alt='phone' />
        <span>{languageMap.whatsApp[language]}</span>
      </a>
    </footer>
  );
}

export default FooterMobile;
